-->
<template>
  <v-card>
    <v-card-text class="px-7">
      <!-- 头顶 -->
      <showDataAccessHeader @add='add'
                            :label="$t('查看同岗位人员数据')"
                            :readonly='readonly'
                            v-model="dataobj.mrbase_sys_data_strategy_same_post_grade_is_used" />
      <!-- 中间内容 -->
      <dataCard v-for="(item,index) in list"
                :readonly='readonly'
                class="mb-7"
                :index=index
                :key='item.uuid'
                :list='item'
                @edit='edit'
                @deleteItem='deleteItem'>
      </dataCard>
      <!-- 弹出框 -->
    </v-card-text>
    <dialogModule v-model="open"
                  :paramsData='currentItem'
                  @getData='getData' />
    <mrc_ess_submitBtn @success='success'
                       v-if="!readonly"
                       :cancelText="$store.state.uuid?'取消':$t('label_The_previous_step')"
                       @cancel='cancel'></mrc_ess_submitBtn>
  </v-card>
</template> 

<script>
import { required } from 'vuelidate/lib/validators'
import {
  listData,
  getData,
  updateData,
  addData,
  delData
} from '@/service/api/data-access-strategy'
import showDataAccessHeader from '../showDataAccessHeader'
import dialogModule from './dialogModule'
import dataCard from '../dataCard'
import tree from '../tree'
export default {
  components: { showDataAccessHeader, dialogModule, dataCard, tree },
  props: {
    readonly: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      open: false,
      params: null,
      currentItem: {},
      currentIndex: {},
      dataobj: {
        mrbase_sys_data_strategy_same_post_grade_is_used: '0'
      },
      list: [],
      deleteList: [] //删除的每一项
    };
  },
  validations: {},
  computed: {},
  created () { },
  watch: {},
  mounted () {
    this.getList()
  },
  methods: {
    async getList () {
      if (this.$store.state.uuid) {
        const res = await getData({ org_level_uuid: this.$store.state.uuid, date: 'mrbase_sys_data_strategy_same_post_grade' })
        if (res.code == 200) {
          this.list = res.mrbase_sys_data_strategy_same_post_grade
          this.dataobj = res.data
        }
      }
      else {

      }
    },
    add () {
      this.currentItem = Object.assign({})
      this.currentIndex = Object.assign({})
      this.open = true
    },
    getData (val, type) { // 0新增 1修改
      this.params = val
      console.log(type);
      if (type == 1) {
        this.$set(this.list, this.currentIndex, Object.assign(this.currentItem, val))
      }
      else {
        this.list.push(val)
      }
    },
    edit (item, index) {  //修改
      console.log(item, index);
      this.currentItem = item
      this.currentIndex = index
      this.open = true
    },
    deleteItem (item, index) { //删除
      this.currentItem = item
      this.currentIndex = index
      if (item.uuid) {
        this.deleteList.push({ ...item, type: 'del' })
      }
      this.list.splice(index, 1)
    },
    success () {
      if (this.$store.state.uuid) {//修改
        this.$emit('success', [...this.list, ...this.deleteList])
      }
      else {//新增
        this.$emit('success', [...this.list, ...this.deleteList])
      }
    },
    cancel () {
      this.$emit('cancel')
    }
  },
};
</script>

